import React, { memo } from 'react';
import { useColorMode } from '../contexts/ColorModeContext';
import { useInspect } from '../contexts/InspectContext';

export default memo(({ data, children }) => {
  const { accessModifier, returnType, arguments: methodArgs, classColor, methodId } = data;
  const { colorMode } = useColorMode();
  const { inspectedMethodId, toggleInspect } = useInspect();
  
  // Create method signature display
  const argsString = methodArgs && methodArgs.length > 0 
    ? methodArgs.map(arg => `${arg.type} ${arg.name}`).join(', ')
    : 'no args';
  
  // Get the appropriate color based on access modifier
  const labelColor = accessModifier === 'public' ? classColor?.light : classColor?.dark;
  
  // Check if this method is being inspected
  const isInspected = inspectedMethodId === methodId;
  
  const handleInspectClick = () => {
    toggleInspect(methodId);
  };
  
  return (
    <div>
      {/* Method name label positioned at top left corner */}
      <div style={{
        position: 'absolute',
        top: '-25px',
        left: '2px',
        backgroundColor: labelColor,
        color: classColor?.contrastText,
        padding: '4px 8px',
        borderRadius: '4px',
        borderBottomLeftRadius: '0px',
        borderBottomRightRadius: '0px',
        fontSize: '12px',
        fontWeight: 'bold',
        zIndex: 10,
      }}>
        {data.label}
        <button 
          onClick={handleInspectClick}
          style={{
            marginLeft: '10px',
            backgroundColor: isInspected ? '#ff4444' : 'transparent',
            color: 'white',
            border: isInspected ? '1px solid #ff6666' : '1px solid transparent',
            borderRadius: '3px',
            padding: '2px 6px',
            fontSize: '10px',
            cursor: 'pointer',
            transition: 'all 0.2s ease'
          }}
        >
          {isInspected ? 'Exit' : 'Inspect'}
        </button>
      </div>
      
      {/* Method signature details */}
      <div style={{
        position: 'absolute',
        top: '-35px',
        left: '0px',
        color: colorMode === 'dark' ? 'white' : 'black',
        padding: '2px 4px',
        borderRadius: '2px',
        fontSize: '8px',
        zIndex: 9,
        maxWidth: '180px',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap'
      }}>
        ({argsString}) → {returnType || 'void'}
      </div>
      
      {/* Group content */}
      {children}
    </div>
  );
}); 